import { Component, OnInit } from '@angular/core';
declare var UnityLoader: any;
declare var UnityProgress: any;
@Component({
  selector: 'app-unity-game',
  templateUrl: './unity-game.page.html',
  styleUrls: ['./unity-game.page.scss'],
})
export class UnityGamePage implements OnInit {

  gameInstance: any;
  appLocation= "../../assets/Unity/Build/Navgrid.json";
  constructor() { }

  ngOnInit() {
    this.gameInstance = UnityLoader.instantiate("gameContainer", this.appLocation, {onProgress: UnityProgress});

  }

  test(){
    this.gameInstance.SetFullscreen(1)
  }



}
